import { Component, ChangeDetectionStrategy, AfterContentChecked } from '@angular/core';
import { ConfigurationService } from "../common/configuration.service";
import { CollectionService } from "../common/collection.service";
import { InheritMetadata } from '../common/inherit-metadata';
import { TooltipTemplateService } from '../common/tooltip-template.service';
import { SeriesComponentGenerated } from './series.component.generated';
import { SeriesItemComponent } from './series-item.component';

/**
 * A collection of one or more series items.
 *
 * @example
 * ```ts
 * import { Component } from '@angular/core';
 *
 * @@Component({
 *   selector: 'my-app',
 *   template: `
 *     <kendo-chart>
 *       <kendo-chart-series>
 *         <kendo-chart-series-item type="line" [data]="[1, 2, 3]">
 *         </kendo-chart-series-item>
 *       </kendo-chart-series>
 *     </kendo-chart>
 *   `
 * })
 * class AppComponent {
 * }
 *
 * ```
 */
@InheritMetadata()
@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [CollectionService],
  selector: 'kendo-chart-series',
  template: ''
})
export class SeriesComponent extends SeriesComponentGenerated implements AfterContentChecked {
  // Place custom properties here

  constructor(protected configurationService: ConfigurationService,
              protected collectionService: CollectionService,
              protected tooltipTemplateService: TooltipTemplateService) {
    super(configurationService, collectionService);
  }

  public ngAfterContentChecked(): void {
      this.readTooltipTemplates();
  }

  private readTooltipTemplates(): void {
    const templates = this.children.map((item) => (<SeriesItemComponent>item).seriesTooltipTemplateRef);
    this.tooltipTemplateService.setSeriesTemplates(templates);
  }
}



